:root {
  --videoWidth: 40px;
}

.video-player {
  /* background-color: red; */
}
.video-url {
  width: 100%;
  border: none;
  outline: none;
  padding: 10px;
  margin-bottom: 10px;
  background-color: rgb(221, 221, 221);
  border-radius: 5px;
  transition: 0.3s;
}
.video-url:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.this-video {
  margin-bottom: 10px;
  height: calc(40px * 9);
  height: calc(var(--videoWidth) * 9);
  width: calc(40px * 16);
  width: calc(var(--videoWidth) * 16);
  transition: 0.3s;
  background-color: yellow;
  position: relative;
}
.in {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.in-icon {
  color: white;
  transition: 0.3s;
  left: 10px;
  top: 10px;
  -webkit-transform: translate(0%, 0%);
          transform: translate(0%, 0%);
  height: 60px;
  width: 60px;
  background-color: rgba(0, 0, 0, 0.794);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.this-video:hover {
  /* transform: scale(1.03); */
}
.this-video:hover > .in-icon {
  -webkit-animation: upload 1s;
          animation: upload 1s;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
  -webkit-animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
          animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
@-webkit-keyframes upload {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes upload {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.this-visible {
  margin-top: 20px;
  display: flex;
  border-top: 2px solid rgb(194, 194, 194);
  border-bottom: 2px solid rgb(194, 194, 194);
  align-items: center;
  padding: 5px;
}

.this-thumbnail {
  overflow: hidden;
  background-color: aqua;
  height: calc(13px * 9);
  width: calc(13px * 16);
  margin: 0 5px;
  border: 1px solid gray;
  cursor: pointer;
}
.this-thumbnail img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.this-content {
  padding: 3px;
  width: 66%;
  display: flex;
  flex-direction: column;
}

.inp {
