  overflow-wrap: break-word;
  font-family: Arial, Helvetica, sans-serif;
  resize: none;
  resize: vertical;
}
.file-inp {
  display: none;
}
.this-title {
  font-size: 18px;
  margin-bottom: 10px;
  resize: none;
}

.this-description {
  font-size: 15px;
  color: gray;
  resize: none;
}
.this-title::-webkit-scrollbar {
  width: 5px;
}
.this-description::-webkit-scrollbar {
